* {
	font-family: 'Roboto', sans-serif; }
header {
	position: relative; }
nav {
	height: 50px;
	background: #212121; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style: none;
	.menu_link {
		color: #FFFFFF;
		font-size: 15px;
		font-weight: 300; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 45px; } }
.subheader {
	position: absolute;
	width: 100%;
	height: 87px;
	background-color: rgba(0,0,0, .6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #FFFFFF;
		font-size: 13px;
		font-weight: 400;
		line-height: 10px; }
	.subheader_call {
		margin-top: 17px;
		color: #FFFFFF;
		font-size: 12px;
		font-weight: 300;
		text-align: right; }
	.subheader_phone {
		display: block;
		margin-top: 4px;
		color: #FFFFFF;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 20px; }
	.subheader_btn {
		margin-top: 18px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0,0,0, .12);
		border: none;
		border-radius: 4px;
		background: linear-gradient(to top,  #1EACC7 0%, #4CE2FF 100%);
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase; } }
.promo {
	min-height: 799px;
	background: url(../img/uber_bg.png) center center/cover no-repeat;
	padding: 155px 0 186px 0;
	.promo_header {
		margin: 0;
		color: #FFF;
		text-align: center;
		font-size: 60px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 60px; }
	.promo_subheader {
		margin: 0;
		margin-top: 22px;
		color: #FFF;
		font-size: 36px;
		text-align: center;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 36px; }
	.promo_descr {
		margin-top: 16px;
		width: 985px;
		color: #FFF;
		text-align: center;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px; }
	.promo_btn {
		display: block;
		margin: 115px auto 0 auto;
		width: 259px;
		height: 63px;
		border: none;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase; } }
.middle {
	padding: 20px 0 64px 0;
	.middle_name {
		margin: 0 auto;
		width: 115px;
		height: 27px;
		background: #000;
		display: flex;
		flex-direction: column;
		justify-content: center;
		color: #FFF;
		text-align: center;
		font-size: 11px;
		font-weight: 400;
		text-transform: uppercase; }
	.middle_header {
		margin: 0;
		margin-top: 33px;
		color: #222;
		text-align: center;
		font-size: 38px;
		font-weight: 700;
		text-transform: uppercase; }
	.middle_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 16px;
		.middle_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 25px;
			width: 116px;
			height: 116px;
			background: #1EACC7;
			border-radius: 100%; }
		.middle_descr {
			width: 371px;
			.middle_subname {
				color: #1EACC7;
				font-size: 17px;
				font-weight: 400;
				line-height: 22px; }
			.middle_text {
				margin-top: 2px;
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px; } } } }
